import React, { createContext, useReducer, useEffect } from "react";
import reducer from "./AppReducer";

const initialState = {
  incomeTransactions:
    JSON.parse(localStorage.getItem("incomeTransactions")) || [],
  expenseTransactions:
    JSON.parse(localStorage.getItem("expenseTransactions")) || [],
};

export const GlobalContext = createContext(initialState);

export const GlobalContextProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    localStorage.setItem(
      "incomeTransactions",
      JSON.stringify(state.incomeTransactions)
    );
    localStorage.setItem(
      "expenseTransactions",
      JSON.stringify(state.expenseTransactions)
    );
  });

  const addIncomeTransaction = (incomeTransaction) => {
    dispatch({ type: "ADD_INCOME", payload: incomeTransaction });
  };

  const addExpenseTransaction = (expenseTransaction) => {
    dispatch({ type: "ADD_EXPENSE", payload: expenseTransaction });
  };

  const deleteTransaction = (id) => {
    dispatch({ type: "DELETE_TRANSACTION", payload: id });
  };

  return (
    <GlobalContext.Provider
      value={{
        incomeTransactions: state.incomeTransactions,
        expenseTransactions: state.expenseTransactions,
        addExpenseTransaction: addExpenseTransaction,
        addIncomeTransaction: addIncomeTransaction,
        deleteTransaction: deleteTransaction,
      }}
    >
      {children}
    </GlobalContext.Provider>
  );
};
